.wrapper {
	max-width: 1289px;
	// min-width: 320px
	margin: 0 auto; }

.container-wrapper {
	padding: 0 20px; }
.content-wrapper {
	max-width: 1124px; }
.out {
	font-family: "GothamPro";
	background: linear-gradient(to bottom, rgba(194,209,218,1) 0%,rgba(194,209,218,0.12) 52%,rgba(194,209,218,1) 100%);
	@include md {
		background: linear-gradient(to bottom,#d4e2ea 0%,rgba(194,209,218,.12) 52%,#ffffff 100%); } }
.overflow {
	overflow: hidden; }

.d-block {
	display: block; }

.d-block-important {
	display: block !important; }

.transition {
	transition: 0.3s; }

.none {
	display: none; }

